@charset "UTF-8";
*::-webkit-input-placeholder {
  color: #202020;
  opacity: 1;
}

*::-moz-placeholder {
  color: #202020;
  opacity: 1;
}

*::-ms-input-placeholder {
  color: #202020;
  opacity: 1;
}

::placeholder {
  color: #202020;
}

::selection {
  background-color: orange;
  color: #fff;
}

html {
  width: 100%;
  /* height: 100%; */
  font-size: 16px;
  line-height: 1.42;
  margin: 0;
  padding: 0;
  -webkit-backface-visibility: hidden;
  /* overflow-x: hidden; */
}

body {
  font-size: 16px;
  min-width: 320px;
  opacity: 1;
  position: relative;
  line-height: 1.42;
  font-family: "Arial", sans-serif;
  overflow-x: hidden;
  color: #000000;
}

body input,
body textarea {
  border: #666 1px solid;
  outline: none;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  border-color: red;
}

body input:required:valid,
body textarea:required:valid {
  border-color: green;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

.header_fon {
  background-color: red;
  min-height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header_content {
  text-align: center;
}

.header_content h1 {
  color: #fff;
  font-size: 4rem;
  margin-bottom: 30px;
}

.in_hea {
  color: orange;
  font-size: 1.32rem;
}

.block {
  padding: 100px 0;
}

.block_title {
  text-align: center;
  font-size: 2rem;
}

.block_first {
  background-color: grey;
}

.heagerclass {
  font-size: 2rem;
}

.heagerclass {
  background-color: red;
  padding: 50px 0;
}

.heagerclass_title {
  color: #fff;
}

.footer_wrap {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  justify-content: space-between;
}

.footer_items {
  width: 30%;
  background-color: yellow;
}

.heagerclass {
  background-color: red;
  padding: 50px 0;
}

.heagerclass_title {
  color: #fff;
}

